import Vue from 'vue'
import Router from 'vue-router'
import Companies from '@/components/Companies'

Vue.use(Router)

export default new Router({
  mode: 'history',
  //base: '/flink_frontend/',
  routes: [
    {
      path: '/',
      name: 'ListCompanies',
      component: Companies
    }
  ]
})



